import btc from "@/assets/btc.png";
import cnc from "@/assets/cnc.png";
import usdt from "@/assets/usdt.png";

import screen from "@/assets/screenIcon.png";
import noConten from "@/assets/zwsj.png";
export default {
  data() {
    return {
      topTitle: "我的钱包",
      btc: btc,
      screen: screen,
      noConten: noConten,
      screenListVal: [],
      affair_type: "",
      DataList: [
        // {
        //   title: "云算一号2期-btc回本套保算力合约备份",
        //   dateTime: "2020-01-20",
        //   number: "99999.99999999",
        //   state: "ok"
        // }
      ],
      list: {
        img: "",
        name: "",
        use: "0",
        rem: "0.0",
        freeze: "0.00000000",
        total: "0.0",
        chain: "",
        coin_type: ""
      },
      // 1日收益 2周收益 3支付 4充币 5提币 6平台手动扣款 7手动转账 8资产充币 9资产提币(选填)
      screenList: [["全部", "日收益", "周收益", "支付", "充币", "提币"]]
    };
  },
  mounted() {
    if (!this.$route.params.itm) {
      this.list = this.$store.state.common.chain_coin_type;
    } else {
      this.$store.dispatch("setchain_coin_type", this.$route.params.itm);
      this.list = this.$route.params.itm;
    }

    this.getList(this.affair_type);
  },
  filters: {
    formatDate: function(value) {
      let val = value * 1000;
      let date = new Date(val);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let m = date.getMinutes();
      m = m < 10 ? "0" + m : m;
      let s = date.getSeconds();
      s = s < 10 ? "0" + s : s;
      return y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;
    }
  },
  methods: {
    getTB() {
      // this.$router.push("/wallet/extractMoney");
      this.$router.push({
        name: "extractMoney",
        params: {
          coin_type: this.list.coin_type
        }
      });
    },
    getCB() {
      // this.$router.push("/wallet/chanrigMoney");
      this.$router.push({
        name: "chanrigMoney",
        params: {
          coin_type: this.list.coin_type
        }
      });
    },
    getList(val) {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          chain: that.list.chain,
          coin_type: that.list.coin_type,
          affair_type: val
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/wallet-balance-detail",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.DataList = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getDetail(val) {
      this.$router.push({
        name: "walletDetail",
        params: {
          id: val
        }
      });
    },
    getType(val) {
      if (val[0] == "全部") {
        this.affair_type = "";
      } else if (val[0] == "日收益") {
        this.affair_type = "1";
      } else if (val[0] == "周收益") {
        this.affair_type = "2";
      } else if (val[0] == "支付") {
        this.affair_type = "3";
      } else if (val[0] == "充币") {
        this.affair_type = "4";
      } else if (val[0] == "提币") {
        this.affair_type = "5";
      }
      this.getList(this.affair_type);
    }
  }
};



